import { NextRequest, NextResponse } from 'next/server';
import prisma from "@/lib/prisma";
import { verifyToken } from '@/utils/authUtils';

export function middleware(req: NextRequest) {
    try {
        console.log(`Middel. called`);

        // Extract token from Authorization header
        const token = req.headers.get('authorization')?.split(' ')[1];
        if (!token) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
        }

        // Verify token and extract admin details
        const decodedAdmin = verifyToken(token);
        if (!decodedAdmin || !decodedAdmin.adminId) {
            return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
        }

        // Fetch the admin from the database
        const admin = await prisma.admin.findUnique({
            where: { id: decodedAdmin.adminId }, // Primary key lookup
            select: {
                id: true,
                name: true,
                email: true,
                role: true,
                createdAt: true,
            },
        });

        if (!admin) {
            return NextResponse.json({ error: "Admin not found" }, { status: 404 });
        }

        return NextResponse.next();
    } catch (error) {
        return NextResponse.json({ error: "Internal Server Error" }, { status: 500 });
    }
}

export const config = {
    matcher: ['/api/admin/list'], // Protect only specific routes
};
